import { PanelBody, SelectControl, ToggleControl } from "@wordpress/components";
import { __ } from "@wordpress/i18n";
import React from "react";
import icons from "../../icons";

const iconPositions = [
  {
    label: 'Right',
    value: 'aab_right_icon',
  },
  {
    label: 'Left',
    value: 'aab_left_icon',
  }
];

export default function AccordionIconPanel({ attributes, setAttributes }) {
  const { showIcon, iconClass, iconPosition } = attributes;

  return (
    <PanelBody
      title={__('Accordion Icon', 'advanced-accordion-block')}
      initialOpen={false}
    >
      <ToggleControl
        label={__('Show Icon', 'advanced-accordion-block')}
        checked={showIcon}
        onChange={(showIcon) => setAttributes({ showIcon })}
      />
      {showIcon && (
        <>
          <SelectControl
            label={__(
              'Select Icon Type',
              'advanced-accordion-block'
            )}
            options={icons}
            onChange={(iconClass) => setAttributes({iconClass})}
            value={iconClass}
          />
          <SelectControl
            label={__(
              'Icon Position',
              'advanced-accordion-block'
            )}
            options={iconPositions}
            onChange={(iconPosition) => {
              setAttributes({iconPosition});
            }}
            value={iconPosition}
          />
        </>
      )}
    </PanelBody>
  );
}