import React from "react";
import {useBlockProps as useDefaultBlockProps} from '@wordpress/block-editor';
import {__} from "@wordpress/i18n";
import AccordionHead from "./accordion-head";
import AccordionBody from "./accordion-body";
import {getBorderStyle} from "../../utils/get-border-style";
import disableQAButton from "../../utils/disable-qa-button";

export default function Output(props) {
    const {isEditor, attributes} = props;
    const useBlockProps = isEditor ? useDefaultBlockProps : useDefaultBlockProps.save;

    const {
        uniqueId,
        disableAccordion,
        makeActive,
        border,
        margins,
        borderRadius,
        anchorLinkShow,
        headingColor,
        customCSS,
        accessibilityOn,
        focusOutlineColor
    } = attributes;

    const aab_premium = aagb_local_object.licensing;
    if (!aab_premium) disableQAButton();
    const borderStyle = getBorderStyle(border);

    return (
        <>
            <div className="custom-css-block">
                <style>
                    {`#aab_accordion_${uniqueId} { ${customCSS} }`}
                    {anchorLinkShow && headingColor && aab_premium &&
                        `
                            #aab_accordion_${uniqueId} .aab__accordion_heading .anchorjs-link { 
                              color: ${headingColor};
                            }
                          `
                    }
                    {accessibilityOn && focusOutlineColor !== "#C2DBFE" &&
                        `#aab_accordion_${uniqueId}:focus-visible {
                              outline: 2px solid ${focusOutlineColor};
                            }
                          `
                    }
                </style>
            </div>
            <div
                {...useBlockProps({
                    className: `aab__accordion_container ${disableAccordion ? 'aab__accordion_disabled' : ''} ${accessibilityOn && 'accessibilityOn'} ${makeActive ? `active__accordion_container_${uniqueId}` : ''}`
                })
                }
                style={
                    {
                        marginTop: margins.top ?? '0px',
                        marginBottom: margins.bottom ?? '0px',
                        ...(borderRadius && {borderRadius: borderRadius + 'px'}),
                        ...borderStyle, // Spread shorthand or individual border values
                    }
                }
                id={`aab_accordion_${uniqueId}`}
                role="button"
                aria-expanded={makeActive}
                tabIndex={accessibilityOn ? 0 : -1}
            >
                <AccordionHead {...props} />
                <AccordionBody {...props} />
            </div>

            {anchorLinkShow === true && aab_premium && !isEditor &&(
                <script>
                    {`
								 document.addEventListener("DOMContentLoaded", () => {
                                        var Anchor1 = new AnchorJS();
                                        Anchor1.add('#aab_accordion_${uniqueId} .aab__accordion_heading .title_wrapper');
                                 });
							`}
                </script>
            )}
        </>
    );
};