import { PanelBody, ToggleControl } from "@wordpress/components";
import { __ } from "@wordpress/i18n";
import React from "react";

export default function AccordionStatusPanel({ attributes, setAttributes }) {
  const { makeActive, disableAccordion } = attributes;

  return (
    <PanelBody
      initialOpen={false}
      title={__('Accordion Status', 'advanced-accordion-block')}
    >
      <ToggleControl
        label={__(
          'Make it Active on Load',
          'advanced-accordion-block'
        )}
        checked={makeActive}
        onChange={(makeActive) => setAttributes({ makeActive }) }
      />
      <ToggleControl
        label={__(
          'Make it a Disable Accordion?',
          'advanced-accordion-block'
        )}
        checked={disableAccordion}
        onChange={(disableAccordion) => setAttributes({ disableAccordion })}
        help={__(
          'No click event works. This feature is for the frontend only!',
          'advanced-accordion-block'
        )}
      />
    </PanelBody>
  );
}