import { PanelBody, TextControl, ToggleControl } from "@wordpress/components";
import { __ } from "@wordpress/i18n";
import React from "react";

export default function FeedbackPanel({ attributes, setAttributes }) {
  const { feedbackShow, feedbacLabel, yesBtn, noBtn, counterShow } = attributes;
  const aab_premium = aagb_local_object.licensing;

  return (
    <PanelBody
      title={__('Feedback', 'advanced-accordion-block')}
      initialOpen={false}
      className={aab_premium ? "" : "aab-pro-element"}
    >
      <ToggleControl
        label={__(
          'Enable / Disable',
          'advanced-accordion-block'
        )}
        disabled={!aab_premium}
        checked={feedbackShow && aab_premium}
        onChange={(feedbackShow) => setAttributes({ feedbackShow })}
      />
      {feedbackShow && (
        <>
          <TextControl
            label={__('Label', 'advanced-accordion-block')}
            disabled={!aab_premium}
            value={feedbacLabel}
            onChange={(feedbacLabel) => setAttributes({ feedbacLabel })}
          />
          <TextControl
            label={__('Yes', 'advanced-accordion-block')}
            disabled={!aab_premium}
            value={yesBtn}
            onChange={(yesBtn) => setAttributes({ yesBtn })}
            className="bbpc-control-half yes-btn"
          />

          <TextControl
            label={__('No', 'advanced-accordion-block')}
            disabled={!aab_premium}
            value={noBtn}
            onChange={(noBtn) => setAttributes({ noBtn })}
            className="bbpc-control-half no-btn"
          />
          <ToggleControl
            label={__(
              'Counter',
              'advanced-accordion-block'
            )}
            disabled={!aab_premium}
            checked={counterShow && aab_premium}
            onChange={(counterShow) => setAttributes({ counterShow })}
          />
        </>
      )}
    </PanelBody>
  );
}