import React from "react";
import { PanelBody } from "@wordpress/components";
import { __ } from "@wordpress/i18n";
import Editor from 'react-simple-code-editor';
import Prism from 'prismjs';

export default function CustomCssStylesPanel({ attributes, setAttributes }) {
  const aab_premium = aagb_local_object.licensing;

  return (
    <PanelBody
      title={__('Custom CSS', 'advanced-accordion-block')}
      initialOpen={false}
      className={aab_premium ? '' : 'aab-pro-element'}
    >
      <label className='custom-css-notice' htmlFor="custom-css">Add your own CSS code here to customize the accordion as per your expectations.</label>
      <Editor
        value={attributes.customCSS}
        disabled={!aab_premium}
        onValueChange={(customCSS) => setAttributes({ customCSS })}
        highlight={(code) => Prism.highlight(code, Prism.languages.css, 'css')}
        padding={10}
        style={{
          fontFamily: 'monospace',
          fontSize: '14px',
          border: '1px solid #ccc',
          borderRadius: '4px',
          minHeight: '100px',
          backgroundColor: '#f9f9f9',
          color: '#333',
          outline: 'none',
          boxSizing: 'border-box',
          marginTop: '7px'
        }}
        />
    </PanelBody>
  );
}