import React from "react";
import { PanelBody } from "@wordpress/components";
import { __ } from "@wordpress/i18n";
import AabColorPicker from "../../../components/aab-colorpicker";

export default function QaIconsStylesPanel({ attributes, setAttributes }) {
  const { QaStyle, qIconColor, qIconBg, aIconColor, aIconBg } = attributes;
  const aab_premium = aagb_local_object.licensing;

  if(!QaStyle || !aab_premium) return <></>

  return (
    <PanelBody
      title={__('Q/A Icons Styles', 'advanced-accordion-block')}
      initialOpen={false}
    >
      <AabColorPicker
        label={__('Q Color ', 'advanced-accordion-block')}
        value={qIconColor}
        onChange={(qIconColor) => setAttributes({ qIconColor })}
        bgValue={qIconBg}
        onBgChange={(qIconBg) => setAttributes({ qIconBg })}
        innerLabel={['Icon', 'Background']}
      />
      <AabColorPicker
        label={__('A Color', 'advanced-accordion-block')}
        value={aIconColor}
        onChange={(aIconColor) => setAttributes({ aIconColor })}
        bgValue={aIconBg}
        onBgChange={(aIconBg) => setAttributes({ aIconBg })}
        innerLabel={['Icon', 'Background']}
      />
    </PanelBody>
  );
}