import { PanelBody, SelectControl, ToggleControl } from "@wordpress/components";
import { __ } from "@wordpress/i18n";
import React from "react";

const anchorPositions = [
  {
      label: 'Left',
      value: 'aab_left_link',
  },
  {
      label: 'Right',
      value: 'aab_right_link',
  },
];

export default function AnchorLinkPanel({ attributes, setAttributes }) {
  const { anchorLinkShow, QaStyle, anchorPosition } = attributes;
  const aab_premium = aagb_local_object.licensing;

  return (
    <PanelBody
      title={__('Anchor Link', 'advanced-accordion-block')}
      initialOpen={false}
      className={aab_premium ? "" : "aab-pro-element"}
    >
      <ToggleControl
        label={__(
          'Anchor Link',
          'advanced-accordion-block'
        )}
        disabled={!aab_premium}
        checked={anchorLinkShow && aab_premium}
        onChange={(anchorLinkShow) =>
          setAttributes({ anchorLinkShow })
        }
      />
        {
          anchorLinkShow && !QaStyle && (
            <SelectControl
              label={__(
                'Anchor Icon Position',
                'advanced-accordion-block'
              )}
              disabled={!aab_premium}
              options={anchorPositions}
              onChange={(anchorPosition) =>
                setAttributes({ anchorPosition })
              }
              value={anchorPosition}
            />
          )
        }
    </PanelBody>
  );
}