import React from "react";
import { __ } from "@wordpress/i18n";
import { Button, PanelBody, SelectControl, ToggleControl } from "@wordpress/components";
import { MediaUploadCheck, MediaUpload } from "@wordpress/block-editor";
import tags from "../../../tags";

export default function AccordionHeadPanel({ attributes, setAttributes }) {
  const { headingTag, QaStyle, showHeadingIcon, headingIconImageUrl, headingIconAlt } = attributes;
  const aab_premium = aagb_local_object.licensing;

  return (
    <PanelBody
      initialOpen={false}
      title={__('Accordion Head', 'advanced-accordion-block')}
    >
      <SelectControl
        label={__(
          'Select Heading Tag',
          'advanced-accordion-block'
        )}
        options={tags}
        onChange={(headingTag) => setAttributes({headingTag})}
        value={headingTag}
      />
      {
        aab_premium && !QaStyle && (
          <ToggleControl
            label={__('Show Heading Icon', 'advanced-accordion-block')}
            checked={showHeadingIcon}
            onChange={() => setAttributes({showHeadingIcon: !showHeadingIcon})}
          />
        )
      }
      {
        showHeadingIcon && !QaStyle && (
          <MediaUploadCheck>
            <MediaUpload
              onSelect={media => {
                setAttributes({
                  headingIconImageUrl: media.url,
                  headingIconAlt: media.alt,
                });
              }}
              allowedTypes={['image']}
              render={({open}) => (
                <Button
                  onClick={open}
                  variant="secondary"
                  icon="format-image"
                >
                  {headingIconImageUrl
                    ? __('Change Heading Icon', 'advanced-accordion-block')
                    : __('Add Heading Icon', 'advanced-accordion-block')}
                </Button>
              )}
            />
          </MediaUploadCheck>
        )
      }
      {
        showHeadingIcon && headingIconImageUrl && !QaStyle && (
          <img
            src={headingIconImageUrl}
            alt={headingIconAlt || __('Heading Icon', 'advanced-accordion-block')}
            style={{
              maxWidth: '100%',
              marginTop: '10px',
            }}
          />
        )
      }
    </PanelBody>
  );
}