import React from "react";
import { RichText as DefaultRichText } from '@wordpress/block-editor';
import {__} from "@wordpress/i18n";
import { getBorderStyle } from "../../utils/get-border-style";

export default function AccordionHead({ isEditor, attributes, setAttributes}) {
    const RichText = isEditor ? DefaultRichText : DefaultRichText.Content;

    const {
        makeActive,
        paddings,
        qIconText,
        qIconColor,
        qIconBg,
        aIconText,
        aIconColor,
        aIconBg,
        heading,
        subheading,
        subheadingColor,
        headingTag,
        anchorLinkShow,
        anchorPosition,
        headingColor,
        showIcon,
        iconClass,
        iconPosition,
        iconColor,
        iconFontSize,
        iconBackground,
        headerBg,
        headingIconImageUrl,
        headingIconAlt,
        showHeadingIcon,
        iconBorder,
        iconBorderRadius,
        QaStyle,
        headingBorder,
        accessibilityOn
    } = attributes;

    const aab_premium = aagb_local_object.licensing;
    const headingBorderStyle = getBorderStyle(headingBorder);
    const calculatedHeaderBg = QaStyle && (headerBg.toLowerCase() === "#bcb6b638") ? "transparent" : headerBg;
    let subheadingPlaceholder = aab_premium ? "Write some subheading" : "Subheading Available on Pro";

    if(!isEditor) subheadingPlaceholder = "";

    return (
        <div
            className={`aab__accordion_head ${iconPosition} ${makeActive ? 'active' : ''} `}
            style={{
                backgroundColor: headerBg ? calculatedHeaderBg : undefined,
                ...(Object.values(paddings).some(val => val) && {
                    padding: `${paddings.top} ${paddings.left} ${paddings.bottom} ${paddings.right}`
                }),
                ...headingBorderStyle
            }}
        >
            <div
                className={`aab__accordion_heading ${iconPosition} ${anchorPosition}`}
            >
                {showHeadingIcon && headingIconImageUrl && !QaStyle && (
                    <div className="heading-icon">
                        <img
                            src={headingIconImageUrl}
                            alt={headingIconAlt || __('Heading Icon', 'advanced-accordion-block')}

                        />
                    </div>
                )}
                {QaStyle && aab_premium && (
                    <div className="icon-container">
                        <div className="icon-q"
                                style={{
                                    color: qIconColor,
                                    backgroundColor: qIconBg
                                }}
                        >{qIconText}</div>
                        <div className="icon-a"

                                style={{
                                    color: aIconColor,
                                    backgroundColor: aIconBg,
                                }}>{aIconText}</div>
                    </div>
                )}
                <div className="head_content_wrapper">
                    <div className="title_wrapper">
                        <RichText
                            tagName={headingTag}
                            value={heading}
                            className="aab__accordion_title"
                            onChange={(heading) =>
                                setAttributes({heading})
                            }
                            style={{
                                margin: 0,
                                color: headingColor,
                            }}
                        />
                        {anchorLinkShow && aab_premium && isEditor && (
                            <a tabIndex={accessibilityOn ? 0 : -1} className="anchorjs-link" href="#">
                                <i className="dashicons dashicons-admin-links"></i>
                            </a>
                        )}
                    </div>
                    <RichText
                        className="aab__accordion_subheading"
                        tagName="p"
                        placeholder={subheadingPlaceholder}
                        value={aab_premium ? subheading : ''}
                        onChange={(value) => {
                            setAttributes({subheading: value});
                        }}
                        onFocus={(e) => {
                            if (!aab_premium) {
                                e.target.blur(); // Prevent focus if aab_premium is false
                            }
                        }}
                        style={{
                            margin: "5px 0 0 0",
                            color: subheadingColor,
                        }}
                    />
                </div>
            </div>
            {showIcon && (
                <div
                    className={`aab__accordion_icon`}
                    style={{
                        ...(iconColor && {color: iconColor}),
                        border: iconBorder ? `${iconBorder.width} ${iconBorder.style} ${iconBorder.color}` : '',
                        ...(iconBorderRadius && {borderRadius: iconBorderRadius}),
                        ...(iconBackground && {backgroundColor: iconBackground})
                    }}
                >
                    <span
                        className={`aab__icon dashicons dashicons-${iconClass}`}
                        style={{
                            fontSize: iconFontSize
                                ? iconFontSize + 'px'
                                : '',
                        }}
                    />
                </div>
            )}
        </div>
    );
};