import React from 'react';
import { InspectorControls } from '@wordpress/block-editor';

import '../editor.scss';
import 'prismjs/themes/prism.css';
import AccordionIdPanel from "../editor/settings/accordion-id";
import AccordionStatusPanel from "../editor/settings/accordion-status";
import AccordionHeadPanel from "../editor/settings/accordion-head";
import AnchorLinkPanel from "../editor/settings/anchor-link";
import AccordionIconPanel from "../editor/settings/accordion-icon";
import FeedbackPanel from "../editor/settings/feedback";
import FaqSchemaPanel from "../editor/settings/faq-schema";
import QaIconsPanel from "../editor/settings/qa-icons";
import QaIconsStylesPanel from "../editor/styles/qa-icons";
import AccordionStylesPanel from "../editor/styles/accordion";
import AccordionHeadStylesPanel from "../editor/styles/accordion-head";
import AccordionIconsStylesPanel from "../editor/styles/accordion-icon";
import AccordionBodyStylesPanel from "../editor/styles/accordion-body";
import CustomCssStylesPanel from "../editor/styles/custom-css";
import AccessibilityPanel from './settings/accessibility';

function Settings(props) {
  return (
    <InspectorControls>
      <AccordionIdPanel {...props} />
      <AccordionStatusPanel {...props} />
      <QaIconsPanel {...props} />
      <AccordionHeadPanel {...props} />
      <AnchorLinkPanel {...props} />
      <AccordionIconPanel {...props} />
      <FeedbackPanel {...props} />
      <FaqSchemaPanel {...props} />
      <AccessibilityPanel {...props} />
    </InspectorControls>
  );
}

function Styles(props) {
  return (
    <InspectorControls group="styles">
      <QaIconsStylesPanel {...props} />
      <AccordionStylesPanel {...props} />
      <AccordionHeadStylesPanel {...props} />
      <AccordionIconsStylesPanel {...props} />
      <AccordionBodyStylesPanel {...props} />
      <CustomCssStylesPanel {...props} />
    </InspectorControls>
  );
}

const Editor = { Settings, Styles };
export default Editor;